var template = require('../../../_hbs/login.hbs');
var UserCollection = require('../collections/UserCollection.js');
var bcrypt = require('bcryptjs');
var User = require('../models/User.js');

var LoginView = Backbone.View.extend({

	template: template,
	tagName: 'div',
	className: 'login form-look',

	events:{
		'click .register-link': 'register',
		'click .login-submit': 'login'
	},

	login: function(e){
		e.preventDefault();

		this.model = new User();
		this.model.set('email',$('.email').val());
		this.$pass = $('.password').val();

		this.collection = new UserCollection({
			email: $('.email').val()
		});

		if($('.email').val() === '' || $('.password').val() === ''){
			if($('.email').val() === ''){
				$('.email').addClass('error');
			}else{
				$('.email').removeClass('error');
			}
			if($('.password').val() === ''){
				$('.password').addClass('error');
			}else{
				$('.password').removeClass('error');
			}
			return;
		}

		$('.email').removeClass('error');
		$('.password').removeClass('error');

		this.listenTo(this.model, "sync", this.render);
		
		this.collection.on('sync', this.renderUsers, this);
		this.collection.fetch();


	},

	renderUsers: function(user){
		console.log(user);
		if(user.length !== 0){
			this.collection.forEach(this.renderUser, this);
		}else{
			$('.email').addClass('error');
			$('.password').addClass('error');
		}
	},

	renderUser: function(user){
		var userpass = bcrypt.compareSync(this.$pass, user.attributes.password);
		if(userpass === true){
			localStorage.setItem('user', JSON.stringify(user));
			this.$user = JSON.parse(localStorage.getItem('user'));
			if(this.$user !== null){
				localStorage.alerted = 'no';
				Window.Application.navigate('home', {trigger: true});
			}
		}else{
			$('.email').addClass('error');
			$('.password').addClass('error');
		}

	},

	errorMessage: function(error){
		return 'Please fill in a ' + error + '.';
	},

	register: function(e){
		e.preventDefault();
		Window.Application.navigate('register', {trigger: true});
	},

	render: function(){
		this.$el.html(this.template());

		return this;
	}

});

module.exports = LoginView;