var template = require('../../../_hbs/register.hbs');
var bcrypt = require('bcryptjs');
var UserCollection = require('../collections/UserCollection.js');
var User = require('../models/User.js');

var RegisterView = Backbone.View.extend({

	template: template,
	tagName: 'div',
	className: 'register form-look',

	events:{
		'click .sing-in': 'login',
		'click .register-submit': 'clickRegister'
	},

	clickRegister: function(e){
		e.preventDefault();
		
		if($('.email').val() === '' || $('.password').val() === '' || $('.password').val() !== $('.re-password').val()){
			if($('.email').val() === ''){
				$('.email').addClass('error');
			}else{
				$('.email').removeClass('error');
			}
			if($('.password').val() === ''){
				$('.password').addClass('error');
			}else{
				$('.password').removeClass('error');
			}
			if($('.re-password').val() === ''){
				$('.re-password').addClass('error');
			}else{
				$('.re-password').removeClass('error');
			}
			if($('.password').val() !== $('.re-password').val()){
				$('.password').addClass('error');
				$('.re-password').addClass('error');
			}
			return;
		}

		$('.email').removeClass('error');
		$('.password').removeClass('error');
		$('.re-password').removeClass('error');

		var salt = bcrypt.genSaltSync(10);
		var hash = bcrypt.hashSync($(".password").val(), salt);

		this.collection.create({
			email: $('.email').val(),
			password: hash
		});

		this.model = new User();
		this.model.set('email',$('.email').val());

		this.collection = new UserCollection({
			email: $('.email').val()
		});
		
		this.collection.on('sync', this.renderUsers, this);
		this.collection.fetch();
	},

	initialize: function(){

		this.collection = new UserCollection();

	},

	renderUsers: function(user){
		this.collection.forEach(this.renderUser, this);
	},

	renderUser: function(user){
		localStorage.setItem('user', JSON.stringify(user));
		this.$user = JSON.parse(localStorage.getItem('user'));
		Window.Application.navigate('home', {trigger: true});
	},

	login: function(e){
		e.preventDefault();
		Window.Application.navigate('login', {trigger: true});
	},

	render: function(){
		this.$el.html(this.template());
		return this;
	}

});

module.exports = RegisterView;