var template = require('../../../_hbs/home.hbs');
var ItemCollection = require('../collections/ItemCollection.js');
var ItemView = require('./ItemView.js');

var HomeView = Backbone.View.extend({


	template: template,
	tagName: 'div',
	className: 'home',
	
	events: {
		'click .logout': 'logout',
		'input .filter': 'inputFilter',
		'click .filter-all': 'filter',
		'click .filter-art': 'filter',
		'click .filter-pic': 'filter',
		'click .filter-vid': 'filter',
	},

	filter: function(e){
		e.preventDefault();

		console.log('klik');

		var target = $(e.currentTarget);
		console.log(target.context.className);

		switch(target.context.className){
			case 'filter-art':
				var input = "text";
				break;

			case 'filter-pic':
				var input = "photo";
				break;

			case 'filter-vid':
				var input = "video";
				break;

			case 'filter-all':
				this.collection.fetch();
				break;

		}
		
		this.renderFilteredItems(this.collection.filterItemsType(input));
	},

	inputFilter:function(e){

		e.preventDefault();
		var input= $(e.currentTarget).val();
		if(input !== ""){

			console.log("succes");
			this.renderFilteredItems(this.collection.filterItems(input));

		}else{

			console.log("all");
			this.collection.fetch();

		}

	},

	renderFilteredItems: function(items){
		console.log(items);
		this.$items.empty();
		items.forEach(this.renderItem, this);
	},

	logout: function(e){
		e.preventDefault();
		localStorage.setItem('alerted','no');
		delete localStorage.user;
		Window.Application.navigate('login', {trigger: true});
	},

	initialize: function(){

		this.$user = JSON.parse(localStorage.getItem('user'));


		this.collection = new ItemCollection({
			user_id: this.$user.id

		});
		this.listenTo(this.collection, 'sync', this.renderItems);
		//this.collection.fetch();
		

	},

	renderItems: function(){
		this.$items.empty();
		this.collection.sort();
		this.collection.forEach(this.renderItem, this);
	},

	renderItem: function(item){

		item.set('online', navigator.onLine);

		var view = new ItemView({
			model: item
		});

		if(navigator.onLine){

			console.log("online");

			view.model.set({showOnline : true});

		}

		switch(item.attributes.type){
			case 'text':
				view.model.set({showTxt : true});
				this.$items.append(view.render().el);
				break;

			case 'photo':
				view.model.set({showPhoto : true});
				this.$items.append(view.render().el);
				break;

			case 'video':
				view.model.set({showVideo : true});
				this.$items.append(view.render().el);
				break;

		}
	},

	render: function(){

		this.$el.html(this.template({
			online: navigator.onLine
		}));

		this.$items = this.$el.find('.items');
		this.$upload = this.$el.find('.upload-btn');
		this.$filter = this.$el.find('.filter');

		this.$filterAll = this.$el.find('.filter-all');
		this.$filterFav = this.$el.find('.filter-fav');
		this.$filterArt = this.$el.find('.filter-art');
		this.$filterPic = this.$el.find('.filter-pic');
		this.$filterVid = this.$el.find('.filter-vid');
		this.$el.find('.username').append(this.$user.email);


		if(navigator.onLine){

			this.collection.fetch({
				success: function(collection, response){
					localStorage.items = JSON.stringify(collection);
				}
			});

		}else{

			this.collection = new ItemCollection(JSON.parse(localStorage.items));
			this.renderItems();

			this.$filter.attr('disabled', 'disabled');
			this.$upload.attr('style', 'display:none;');

			this.$filterAll.attr('style', 'display:none;');
			this.$filterFav.attr('style', 'display:none;');
			this.$filterArt.attr('style', 'display:none;');
			this.$filterPic.attr('style', 'display:none;');
			this.$filterVid.attr('style', 'display:none;');

		}


		return this;

	}

});

module.exports = HomeView;