var template = require('../../../_hbs/item.hbs');

var ItemView = Backbone.View.extend({

	template: template,

	tagName: 'article',
	className: 'item col-xs-12 col-sm-6 col-md-4 col-lg-4',

	events: {
		'click .delete': 'clickDelete',
	},

	clickDelete: function(e){
			e.preventDefault();
			this.model.destroy();
	},

	initialize: function(){
			//view verwijderen:
			this.listenTo(this.model, 'destroy', this.remove);
	},

	render: function(){
		this.$el.html(this.template(this.model.attributes));
		return this;
	}


});

module.exports = ItemView;