var ItemCollection = require('../collections/ItemCollection.js');
var template = require('../../../_hbs/upload.hbs');

var UploadView = Backbone.View.extend({

	template: template,
	tagName: 'div',
	className: 'upload form-look',

	events : {

		'click .item-submit': 'addItem',
		'click .text-submit': 'addText',
		'click .logout': 'logout',
		
	},

	initialize: function(options){

		this.$user = JSON.parse(localStorage.getItem('user'));

		this.collection = new ItemCollection();

	},

	logout: function(e){
		e.preventDefault();
		localStorage.setItem('alerted','no');
		delete localStorage.user;
		Window.Application.navigate('login', {trigger: true});
	},

	addText: function(e){
		e.preventDefault();


		if(this.$el.find('.description_text').val() === ""){
			return;
		}

		this.collection.create({
     		 tag: this.$el.find('.tag_text').val(),
     		 description: this.$el.find('.description_text').val(),
     		 user_id: this.$user.id,

		});

		Window.Application.navigate('home', {trigger: true});
	
	},

	addItem: function(e){

		e.preventDefault();

		

		var $form = this.$el.find('form');
		var $image = this.$el.find('.image');
		var $tag = this.$el.find('.tag');
		var $description = this.$el.find('.description');

		if($image.val() === "" || $description.val() === "" || $tag.val() === ""){
			return;
		}

		var file = $image.get(0).files[0];

		//check for image!!!!

		//geen image
		// if(file.type.search('image') < 0){
		// 	$image.val('');
		// 	return;
		// }

		var data = new FormData($form.get(0));


		//this.user.get('id')
		data.append( 'user_id', this.$user.id);

		$.ajax({

			url: 'api/upload',
			data: data,
			type: 'POST',
			enctype: 'multipart/form-data',
			processData: false,
			contentType: false

		}).done(function(data){

			Window.Application.navigate('home', {trigger: true});

		}.bind(this));


	},

	render: function(){

		this.$el.html(this.template());

		return this;

	}

});

module.exports = UploadView;