
var WelcomeView = require('../views/WelcomeView.js');
var LoginView = require('../views/LoginView.js');
var RegisterView = require('../views/RegisterView.js');
var HomeView = require('../views/HomeView.js');
var ItemDetailView = require('../views/ItemDetailView.js');
var UploadView = require('../views/UploadView.js');

var Application = Backbone.Router.extend({


	routes: {
		//pagina: functie

		"welcome": "welcome",
		"login": "login",
		"register": "register",
		"home": "home",
		"detail/:id": "detail",
		"upload": "upload",

		"*actions": "default"
		
	},

	empty: function(){
		//container clearen
		$('.container-fluid').empty();
	},

	welcome: function(){
		this.empty();
		this.welcome = new WelcomeView();
		$('.container-fluid').append(this.welcome.render().el);
	},

	login: function(){

		this.empty();

		if(!localStorage.user){
			this.login = new LoginView();
			$('.container-fluid').append(this.login.render().el);
		}else{
			Window.Application.navigate('home', {trigger: true});
		}

	},

	register: function(){
		
		this.empty();

		if(!localStorage.user){
			this.register = new RegisterView();
			$('.container-fluid').append(this.register.render().el);
		}else{
			Window.Application.navigate('home', {trigger: true});
		}
		
	},

	home: function(){

		this.empty();

		if(!localStorage.user){
			Window.Application.navigate('welcome', {trigger: true});
		}else{
			
			$(".login-message").fadeIn('fast');
			setTimeout(function() { $(".login-message").fadeOut('fast'); }, 1000);
			localStorage.setItem('alerted', 'yes');
			
			this.homeview = new HomeView();
			$('.container-fluid').append(this.homeview.render().el);
		}
		
	},

	detail: function(id){
		this.empty();
		this.detail = new ItemDetailView({
			id: id
		});
		$('.container-fluid').append(this.detail.render().el); 
		//console.log('detail', id);
	},

	upload: function(){

		this.empty();

		if(!localStorage.user){
			Window.Application.navigate('welcome', {trigger: true});
		}else{
			
			$(".login-message").fadeIn('fast');
			setTimeout(function() { $(".login-message").fadeOut('fast'); }, 1000);
			localStorage.setItem('alerted', 'yes');
			
			this.uploadview = new UploadView();
			$('.container-fluid').append(this.uploadview.render().el);
		}
		
	},


	default: function(){
		
		this.navigate("home", {trigger: true});
		
	},




});

module.exports = Application;