var template = require('../../../_hbs/detail.hbs');
var Item = require('../models/Item.js');


var ItemDetailView = Backbone.View.extend({

	template: template,
	tagName: 'div',
	className: 'detail',

	events: {

		'click .logout': 'logout',

	},

	logout: function(e){
		e.preventDefault();
		localStorage.setItem('alerted','no');
		delete localStorage.user;
		Window.Application.navigate('login', {trigger: true});
	},

	initialize: function(options){



		if(options && options.id){

			this.model = new Item();

			this.model.set('id', options.id);
			this.model.fetch({
				success: function(model, response){

					switch(model.attributes.type){
						case 'text':
							model.set({showTxt : true});
							break;

						case 'photo':
							model.set({showPhoto : true});
							break;

						case 'video':
							model.set({showVideo : true});
							break;
					}

					if(response.length === 0){
						Window.Application.navigate('home', {trigger:true});
					}

				}
			});	

			this.listenTo(this.model, "sync", this.render);		

		}
	},

	render: function(){

		
		this.$el.html(this.template(this.model.attributes));
		return this;
	}
	
});

module.exports = ItemDetailView;