var Item = require('../models/Item.js');


var ItemCollection = Backbone.Collection.extend({


	model: Item,
	url: 'api/items',

	initialize: function(options){
		if(options){
			this.id = options.id;
			this.user_id = options.user_id;
		}
	},

	filterItems: function(query){
		return this.filter(function(item){
			return item.get('tag').toLowerCase().indexOf(query) > -1;
		});
	},
	filterItemsType: function(query){
		return this.filter(function(item){
			return item.get('type').toLowerCase().indexOf(query) > -1 ;
		});
	},

	//sorteren van tweets, .sort oproepen voor je je tweets rendert
	comparator: function(item) {
		return - item.get("id");
	},

	sync: function(method, model, options) {
		if(model.methodUrl && model.methodUrl(method.toLowerCase())) {
			options = options || {};
			options.url = model.methodUrl(method.toLowerCase());
		}
    Backbone.Collection.prototype.sync.apply(this, arguments);
	},

	methodUrl: function(method){
		if(method === 'read' && this.user_id){

			this.url = 'api/items/' + this.user_id;
			return;
		}


		this.url= 'api/items';

	},

});

module.exports = ItemCollection;